<template>
  <v-container
    id="details"
    fluid
    tag="section"
  >
    <base-material-card
      icon="mdi-clipboard-text"
      title="Отчеты"
      class="px-5 py-3"
    >
      <v-simple-table>
        <thead>
        <tr>
          <th class="text-right primary--text">
            Номер ID
          </th>
          <th class="text-right primary--text">
            Источник
          </th>
          <th class="text-right primary--text">
            Дата Бронирования
          </th>
          <th class="text-right primary--text">
            Категория/Номер
          </th>
          <th class="text-right primary--text">
            Заезд
          </th>
          <th class="text-right primary--text">
            Выезд
          </th>
          <th class="text-right primary--text">
            ФИО
          </th>
          <th class="text-right primary--text">
            Контакты
          </th>
          <th class="text-right primary--text">
            Стоимость
          </th>
          <th class="text-right primary--text">
            Оплачено
          </th>
          <th class="text-right primary--text">
            Долг
          </th>
          <th class="text-right primary--text">
            Статус
          </th>
          <th class="text-right primary--text">
            Сотрудник
          </th>
        </tr>
        </thead>
        <tbody>
        <tr
          v-for="(data, i) in $store.state.config.chart.items"
          v-bind:key="data"
        >
          <td class="text-right">
            {{ i }}
          </td>
          <td class="text-right">
            {{ data.reservSource }}
          </td>
          <td class="text-right">
            {{ new Date(data.time.start).toISOString().substr(0, 10) }}
          </td>
          <td class="text-right">
            {{ data.rowId }}
          </td>
          <td class="text-right">
            {{ new Date(data.time.start).toISOString().substr(0, 10) }}
          </td>
          <td class="text-right">
            {{ new Date(data.time.end).toISOString().substr(0, 10) }}
          </td>
          <td class="text-right">
            {{ getName(data) }}
          </td>
          <td class="text-right">
            {{ data.phone }} / {{ data.email }}
          </td>
          <td class="text-right">
            ${{ data.cost }}
          </td>
          <td class="text-right">
            ${{ data.paid }}
          </td>
          <td class="text-right">
            ${{ data.cost - data.paid }}
          </td>
          <td class="text-right">
            {{ status[data.status] }}
          </td>
          <td class="text-right">
            0
          </td>
        </tr>
        </tbody>
      </v-simple-table>
    </base-material-card>

    <div class="py-3" />
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        status: {
          undefined: 'Не подтверждено',
          accepted: 'Подтверждено',
          cancelled: 'Отменено',
          booking: 'Забронировано',
          out: 'Выезд',
          in: 'Назаезд',
          halfBooking: 'Частично оплачен',
          reserve: 'Резерв',
          overbooking: 'Overbooking',
        },
      }
    },
    methods: {
      getName (data) {
        return data.firstName + ' ' + data.lastName + ' ' + data.patronymic
      },
    },
  }
</script>
